import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';

@Component({
  selector: 'zk-contacts',
  templateUrl: 'contacts.html'
})
export class ZKContact {

  constructor(private alertCtrl: AlertController, public navCtrl: NavController) {

  }

  alertWechat(title) {
    let alert = this.alertCtrl.create({
      title: '微信咨询号码添加提示',
      subTitle: title,
      buttons: ['确定']
    });
    alert.present();
  }

  alertEmail(title) {
    let alert = this.alertCtrl.create({
      title: '邮件联系提示',
      subTitle: title,
      buttons: ['确定']
    });
    alert.present();
  }

  wechat(event) {
    let title = "如无法自动添加微信好友，请手动添加微信号：chenrain521。";
    try {
      var textarea = document.createElement("textarea");
      textarea.textContent = "chenrain521";
      textarea.style.position = "fixed";
      textarea.style.top = "-1000px";

      document.body.appendChild(textarea);
      textarea.select();
      var successful = document.execCommand('copy');
      document.body.removeChild(textarea);
      
      if (successful) {
        title += "\n微信号已经成功复制到系统！";
      }

    } catch (err) {
      console.log(err);
    }
    this.alertWechat(title);
    return false;
  }

  mail(event) {
    let title = "如遇无法自动打开邮件发送，请手动发送邮件到：soft@info-soft.cn。";
    try {
      var textarea = document.createElement("textarea");
      textarea.textContent = "soft@info-soft.cn";
      textarea.style.position = "fixed";
      textarea.style.top = "-1000px";
      document.body.appendChild(textarea);
      textarea.select();
      var successful = document.execCommand('copy');
      if (successful) {
        title += "邮箱已经成功复制到系统！";
      }
    } catch (err) {
      console.log(err);
    }
    this.alertEmail(title);
    return false;
  }

}



